<template>
  <section class="grid-header">
    <div class="grid-header__filters">
      <Input
        v-model="textFilter"
        hide-shadow
        :placeholder="t('swaps.table.filters.text')"
        class="grid-header__filter grid-header__filter-search"
      >
        <template #prepend>
          <Icon name="search" />
        </template>
      </Input>
      <DateRange
        v-model="dateRange"
        :placeholder-from="t('common.from')"
        :placeholder-to="t('common.to')"
        class="grid-header__filter grid-header__filter-date-range"
      />
      <div class="grid-header__filter grid-header__filter-crypto">
        Crypto <Icon name="arrow-down" />
      </div>
    </div>
    <div class="grid-header__settings">
      <RadioButtons
        v-model="status"
        :data="radioButtons"
      >
        <template #default="{ item }">
          {{ t('swaps.table.filters.' + item.key) }}
        </template>
      </RadioButtons>
      <div class="grid-header__settings-table settings-table">
        <Dropdown class="settings-table__count">
          <template #label>
            <span class="settings-table__label settings-table__count__label">
              {{ t('table.showOnPage') }}: {{ perPage }}
              <Icon name="arrow-down" class="settings-table__count__label-icon" />
            </span>
          </template>
          <template #body>
            <Container bordered box-shadow background-color="white" class="settings-table__count__body">
              <div
                v-for="item in pageSettings"
                :key="item"
                class="settings-table__count__item"
                @click="changeItemsPerPage(item)"
              >
                {{ item }}
              </div>
            </Container>
          </template>
        </Dropdown>
        <SelectColumns :columns="columns" />
      </div>
    </div>
  </section>
</template>

<script>
import { computed, reactive, ref } from 'vue'
import Input from '@/shared/components/UIKit/Input'
import Icon from '@/shared/components/Icon/Icon'
import DateRange from '@/shared/components/UIKit/DateRange'
import RadioButtons from '@/shared/components/UIKit/RadioButtons'
import Dropdown from '@/shared/components/UIKit/Dropdown'
import Container from '@/shared/components/Container'
import { useStore } from 'vuex'
import SelectColumns from '@/admin/components/Table/SelectColumns'
import { useI18n } from 'vue-i18n'

export default {
  name: 'GridHeader',
  components: { SelectColumns, Container, Dropdown, RadioButtons, DateRange, Icon, Input },
  setup () {
    const { t } = useI18n()
    const store = useStore()
    const columns = computed(() => store.getters['swaps/getColumns'])

    const textFilter = ref('')
    const status = ref(null)
    const perPage = ref(100)
    const dateRange = ref({
      from: '',
      to: ''
    })
    const pageSettings = reactive([100, 200, 500])
    const radioButtons = reactive([
      { key: 'all', title: 'All' },
      { key: 'complete', title: 'Complete' },
      { key: 'progress', title: 'In Progress' },
      { key: 'hold', title: 'On Hold' },
      { key: 'expired', title: 'Expired' }
    ])

    const changeItemsPerPage = (item) => {
      perPage.value = item
    }
    return {
      textFilter,
      dateRange,
      radioButtons,
      status,
      columns,
      pageSettings,
      perPage,
      t,
      changeItemsPerPage
    }
  }
}
</script>

