      }
    }
    &__body {
      min-width: 180px;
      padding: 20px;
      border-radius: 10px;

    }
    &__item {
      cursor: pointer;

      &:not(:first-child):not(:last-child) {
        margin: 12px 0;
      }
      .checkbox {
        width: 100%;

        &__label {
          font-size: 14px;
        }
      }
    }
