<template>
  <section class="swaps">
    <header class="swaps__header">
      <Heading>{{ t('swaps.title') }}</Heading>
    </header>
    <div class="swaps__container">
      <SwapsGrid />
    </div>
  </section>
</template>

<script>
import { onBeforeUnmount } from 'vue'
import { useI18n } from 'vue-i18n'
import { useStore } from 'vuex'
import swapsModule from '@/admin/store/modules/swaps'
import Heading from '@/admin/components/Heading'
import SwapsGrid from '@/admin/views/Swaps/SwapsGrid'
import { MODULES_NAMES } from '@/shared/common/modules'

export default {
  name: 'Swaps',
  components: {
    Heading,
    SwapsGrid
  },
  setup () {
    const { t } = useI18n()
    const store = useStore()
    if (!store.hasModule(MODULES_NAMES.SWAPS)) {
      store.registerModule(MODULES_NAMES.SWAPS, swapsModule)
    }
    onBeforeUnmount(() => {
      if (store.hasModule(MODULES_NAMES.SWAPS)) {
        store.unregisterModule(MODULES_NAMES.SWAPS)
      }
    })
    return {
      t
    }
  }
}
</script>

